<template>
	<view>
		<uni-search-bar :placeholder="searchPlaceHolder" @confirm="search" v-model="keyword"></uni-search-bar>
		<view class="firesearch">
			<image src="../../static/icon/fire.png" mode="scaleToFill" class="fire"></image>
			<text>热搜榜</text>
		</view>
		<view class="hotlist">
			<view class="lefthot">
				<navigator :class="{bold:index<='2'?true:false}" v-for="(item,index) in hotlist" v-if='(index+2)%2==0' :key="index"
				 url=""><text :class="{lessthanten:index<='8'?true:false}">{{index+1}}</text>{{item.searchWord}}</navigator>
			</view>
			<view class="righthot">
				<navigator :class="{bold:index<='2'?true:false}" v-for="(item,index) in hotlist" v-if='(index+2)%2==1' :key="index"
				 url=""><text :class="{lessthanten:index<='8'?true:false}">{{index+1}}</text>{{item.searchWord}}</navigator>
			</view>
		</view>
		<button :class="{hotserchmore:true, iconfont:true,hide:showView}" plain @click="moreHotlist($event)">展开更多热搜&#xe791;</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				keyword: '',
				searchPlaceHolder: '',
				realplaceHolder: '',
				hotlist: [],
				showView: false
			};
		},
		methods: {
			async getPlaceHolder() {
				const [error, res] = await uni.request({
					url: 'http://localhost:3000/search/default'
				})
				if (res.data.code !== 200) {
					return false
				}
				this.searchPlaceHolder = res.data.data.showKeyword
				this.realplaceHolder = res.data.data.realkeyword
			},
			async getHotList() {
				const [error, res] = await uni.request({
					url: 'http://localhost:3000/search/hot/detail'
				})
				if (res.data.code !== 200) {
					return false
				}
				this.hotlist = res.data.data.slice(0, 10)
			},
			async moreHotlist(val) {
				const [error, res] = await uni.request({
					url: 'http://localhost:3000/search/hot/detail'
				})
				if (res.data.code !== 200) {
					return false
				}
				this.hotlist = res.data.data
				this.showView = true
			}
		},
		created() {
			this.getPlaceHolder()
			this.getHotList()
		}
	}
</script>

<style lang="less">
	@import "../../static/fonts/iconfont.css";

	.firesearch {
		height: 60rpx;
		border-bottom: 1px solid #E6E6E6;
		text-align: left;

		text {
			margin: 0;
			height: 40rpx !important;
			font-size: 30rpx;
			vertical-align: middle;
		}

		.fire {
			margin-left: 30rpx;
			height: 30rpx;
			width: 30rpx;
			vertical-align: middle;
		}

	}

	.hotlist::after {
		content: "";
		display: block;
		height: 0;
		clear: both;
		visibility: hidden;

	}

	.lefthot {
		width: 46%;
		float: left;
		margin-top: 12rpx;
		margin-left: 4%;

		navigator {
			font-size: 30rpx;
			margin-bottom: 20rpx;
			display: block;
			overflow: hidden; //超出的文本隐藏
			text-overflow: ellipsis; //用省略号显示
			white-space: nowrap; //不换行
		    text{
		    	margin-right: 15rpx;
		    }
			.lessthanten{
				margin-right: 30rpx !important;
			}
		}
	}

	.righthot {
		width: 50%;
		float: left;
		margin-top: 12rpx;

		navigator {
			font-size: 30rpx;
			margin-bottom: 20rpx;
			display: block;
			overflow: hidden; //超出的文本隐藏
			text-overflow: ellipsis; //用省略号显示
			white-space: nowrap; //不换行
		    text{
				margin-right: 15rpx;
			}
			.lessthanten{
				margin-right: 30rpx !important;
			}
		}
	}

	.hotserchmore {
		margin-top: 30rpx;
		text-align: center;
		font-size: 20rpx;
		color: #808080;
	}

	.hide {
		display: none;
	}

	.bold {
		font-weight: bold;
		text{
			color: #EC4141;
		}
	}
</style>
